import Vue from 'vue'
import Vuex from 'vuex'
import CardBox from '@/scripts/pool/cardBox'
import api from './api';

Vue.use(Vuex)

const STORAGE_PREFIX = "datastore_pool_";
const STORAGE_INDEX = "datastore_index";

var cardBox = new CardBox();

const state = {
  meta: null,
  localMeta:null,
  cardBox: cardBox,
  currentGame: {
    id: 'custom',
    name: '自訂義',
    error: {
      isError: false
    }
  }
}

let defaultGameSet = {
  id: 'custom',
  name: '自訂義',
  error: {
    isError: false
  }
}


export default new Vuex.Store({
  state: state,
  mutations: {
    setCurrentGame(state, data) {
      if (typeof state.currentGame != 'object') {
        state.currentGame = defaultGameSet;
      }
      state.currentGame = data;
    },
    saveGame(state) {
      if (typeof state.currentGame != 'object') {
        state.currentGame = defaultGameSet;
      }
      var model = state.cardBox.toModel();
      var json = JSON.stringify(model)
      var target = state.currentGame;
      window.localStorage.setItem(STORAGE_PREFIX + target.id, json);
    },
    loadGame(state, data) {
      state.cardBox.clear();
      state.cardBox.setModel(data);
    },
    loadMeta(state, payload) {
      state.meta = payload;
    },
    loadLocalMeta(state) {
      var json = window.localStorage.getItem(STORAGE_INDEX);
      var model = JSON.parse(json);
      state.localMeta.games = model;
    },
    setGameError(state, payload){
      if(payload)
      {
        state.isError = payload.isError || false;
        state.message = payload.message || [];
        state.exception = payload.exception || null;
      }
    }
  },
  actions: {
    loadGame(context, payload) {
      let target = context.getters.games.find(g => g.id == payload.id);
      if (payload.loadCustom || !target) {
        let json = window.localStorage.getItem(STORAGE_PREFIX + payload.id);
        if (json) {
          let model = JSON.parse(json);
          context.commit('loadGame', model);
          return model;
        }
      }
      if (target) {
        context.commit('setCurrentGame', target);
        if (typeof target.data == 'object') {
          let data = target.data;
          context.commit('loadGame', data);
          return data;
        }
        else if (typeof target.data == 'string') {
          return api.fetchGame(target.data).then(function (response) {
            let data = response.data;
            context.commit('loadGame', data);
            return data;
          }).catch(function (e) {
            console.log('獎池載入錯誤',e);
            context.commit('loadGame', {});
            context.commit('setGameError', {
              isError:true, 
              message:['無法載入指定資料'],
              exception:e});
            });
        }
      }
      context.gameId = payload;

    },
    saveGame(context) {
      context.commit('saveGame');
      return context.currentGame;
    },
    loadMeta(context) {
      return api.fetchMeta().then(function (response) {
        var meta = response.data;
        context.commit('loadMeta', meta);
        return meta;
      });
    }
  },
  getters: {
    pools(state) {
      return state.cardBox.pools;
    },
    cards(state) {
      return state.cardBox.cards;
    },
    cardGroups(state) {
      return state.cardBox.groups;
    },
    games(state) {
      if (state.meta)
        return state.meta.games;
      else
        return [];
    },
    meta(state) {
      return state.meta
    },
    currentGame(state){
      return state.currentGame;
    }
  }
})

