function randomId(prefix, notWithIn, baseLength = 5){
    if(baseLength < 1)
    {
        baseLength = 1;
    }
    var rnd = '';
    var round = 0;
    for(let i = 0; i< 100; i++) {
        var length = baseLength+Math.round(round/10);
        rnd = prefix + Math.random().toString(36).substr(2, length);
        if(!Array.isArray(notWithIn) || notWithIn.findIndex(e=>e==rnd) < 0){
            break;
        }
    } 
    return rnd;
}

export {randomId}