<template>
  <div class="menu-panel">
    <div class="menu-header">{{title}}</div>
    <div class="menu-body">
      <div
        class="menu-item"
        v-for="item in items"
        :key="item.id"
        @click="clickItem(item)"
      >{{item.name}}</div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    items: {
      type: Array
    },
    title:{
      type: String
    }
  },
  methods: {
    clickItem(item) {
      this.$emit("select", item);
    }
  }
};
</script>

<style lang="scss">
@import '@/assets/color.scss';
.menu-panel {
  background: $c-bg;
  color: $c-text;
  border: 2px solid;
  width: 500px;
  display: inline-block;
  cursor: default;
  
  .menu-header {
  padding: 5px;
  font-size: 1.2rem;
  background: $c-panel-header-bg;
  color: $c-panel-header-text;
}

.menu-item {
  cursor: pointer;
  padding: 5px;
}

.menu-item:hover {
  background: $c-panel-hover-bg;
}

}


</style>