<template>
  <div class="page home">
    <div class="page-title">
      抽卡模擬器
    </div>
    <menu-panel :items="games" :title="'遊戲'" @select="chooseGame" />
  </div>
</template>

<script>

import CompMenuPanel from "@/components/common/MenuPanel";

export default {
  name: 'Home',
  components: {
    "menu-panel": CompMenuPanel
  },
  data(){
    return {
    }
  },
  methods:{
    chooseGame(item){
      this.$router.push({
        name:'poolIndex',
        params:{game:item.id}
        });
    }
  },
  computed:{
    games(){
      return this.$store.getters.games;
    }
  },
}
</script>

