function Card(param){
    this.id = '';
    this.group = '';
    this.name = '';
    this.image = '';
    Object.assign(this,param);
}

Card.prototype.toModel = function(){
    return {
        id: this.id.toString(),
        group: this.group.toString(),
        name:this.name.toString(),
        image:this.image.toString()
    };
}

export default Card;