import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home/Index.vue'
import store from '../store/index';

Vue.use(VueRouter)

  const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/:game/pool',
    name: 'poolIndex',
    component: () => import(/* webpackChunkName: "pool" */ '../views/pool/Index.vue'),
  },
  {
    path: '/:game/pool/:id',
    name: 'pool',
    component: () => import(/* webpackChunkName: "pool" */ '../views/pool/Board.vue'),
  },
  {
    path: '/:game/config',
    name: 'poolConfig',
    component: () => import(/* webpackChunkName: "pool" */ '../views/config/Index.vue'),
  },
  {
    path: '*',
    component: () => import(/* webpackChunkName: "common" */ '../views/NotFound.vue')
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach((to,from,next)=>{
  if(!store)
  {
    next();
  }
  var gameId = to.params.game;
  if(store.getters.meta == null || typeof store.getters.meta != 'object')
  {
    store.dispatch('loadMeta').then(function(){
      store.dispatch('loadGame',{id:gameId}).then(function(){
        next();
      })
    })
    return;
  }
  else if(store.getters.currentGame == null
    || store.getters.currentGame.id != gameId){
      store.dispatch('loadGame',{id:gameId}).then(function(){
        next();
      })
      return;
  }
  next();
})



export default router
