import PoolGroup from './poolGroup';

function Pool(params) {

    this.groups = [];
    this.cardBox = null;
    this.guarantee = [];
    this.name = 'untitled';
    this.id = '';

    this.assign(params);

}

///抽一張
Pool.prototype.choose = function () {
    var groups = this.groups.filter(g=>g.cards.length > 0);
    if(groups.length > 0)
    {
        var group = _chooseWithRate(groups);
        var cards = _chooseCardsInGroup(group);
        var card = _choose(cards);
        return card;
    }
    else{
        console.log('No avaliable card in this pool')
        return null;
    }

}

Pool.prototype.assign = function(params){
    if (typeof params == 'object') {
        Object.assign(this, {
            id: params.id,
            name: params.name,
            cards: params.cards
        });
        if (Array.isArray(params.groups)) {
            this.groups.splice(0,this.groups.length);
            for (let i = 0; i < params.groups.length; i++) {
                const group = params.groups[i];
                this.addGroup(group);
            }
        }
    }
}

//加入抽卡群組
Pool.prototype.addGroup = function (param) {
    var group = new PoolGroup(param);
    let that = this;
    //來自Cardbox
    Object.defineProperty(group, '_cardsInBox', {
        get() {
            return that.chooseCardsInGroup(this.groupId);
        }
    });
    //整合性的Card清單
    Object.defineProperty(group, '_groupInfo', {
        get() {
            if (!that.cardBox)
            {
                return {};
            }
            else{
                return that.cardBox.groups.find(g => g.id == this.groupId) || {};
            }
        }
    });
    this.groups.push(group);
    return group;
}

///自Cardbox載入群組
Pool.prototype.loadCardboxGroups = function (trimDeprecatedGroup=true) {
    if (!this.cardBox) {
        console.error('You need add cardbox before load cards');
        return;
    }
    var existedKeyInBox = {};
    for (let i = 0; i < this.cardBox.groups.length; i++) {
        const cardboxGroup = this.cardBox.groups[i];
        existedKeyInBox[cardboxGroup.id] = '';
        let groupIndexInPool = this.groups.findIndex(e => e.groupId == cardboxGroup.id);
        if (groupIndexInPool < 0) {
            this.addGroup({
                groupId: cardboxGroup.id,
                rate: cardboxGroup.rate,
                cards: []
            });
        }
        else {
            let targetGroup = this.groups[groupIndexInPool]
            targetGroup.name = cardboxGroup.name;
            targetGroup.rate = targetGroup.rate || cardboxGroup.rate;
        }
    }

    if(trimDeprecatedGroup){
        for (let i = this.groups.length - 1; i >= 0; i--) {
            if(existedKeyInBox[this.groups[i].groupId] === undefined)
            {
                this.groups.splice(i, 1);
            }
        }
    }

}

Pool.prototype.chooseCardsInGroup = function (groupId) {
    if (!this.cardBox)
        return [];
    else
        return this.cardBox.cards.filter(c => c.group == groupId);
}

Pool.prototype.toModel = function () {
    var result = {
        id: this.id,
        name: this.name.toString(),
        groups: this.groups.map(e => e.toModel()),
    }
    return result;
}

Pool.prototype.removeInvalidCards = function(){
    const that = this;
    for (let i = 0; i < that.groups.length; i++) {
        const group = that.groups[i];
        if(that.cardBox)
        {
            for (let j = group.cards.length - 1; j >= 0; j--) {
                const cardId = group.cards[j];
                if(that.cardBox.cards.findIndex(m=>m.id == cardId) < 0)
                {
                    group.cards.splice(j,1);
                }
            }
        }
        if(typeof group.up == 'object' && Array.isArray(group.up.cards))
            {
                for (let j = group.up.cards.length -1 ; j >= 0; j--) {
                    const cardId = group.up.cards[j];
                    if(group.cards.findIndex(m=>m == cardId) < 0)
                    {
                        group.up.cards.splice(j,1);
                    }
                }
            }
    }
}

///

function _getRateSum(list) {
    var totalRate = 0;
    for (let i = 0; i < list.length; i++) {
        const item = list[i];
        item.rate = Number(item.rate);
        if (Number.isFinite(item.rate) && item.rate > 0) {
            totalRate += item.rate;
        }
    }
    return totalRate;
}



// choose a item from array, accept rate
function _chooseWithRate(list) {
    var totalRate = _getRateSum(list);
    if (totalRate == 0) {
        return _choose(list);
    }
    else {
        let magicNumber = Math.random() * totalRate;
        let appendedBase = 0;
        for (let i = 0; i < list.length; i++) {
            const item = list[i];
            appendedBase += (item.rate || 0);
            if (magicNumber < appendedBase) {
                return item;
            }
        }
    }

}


function _choose(list) {
    var totalRate = list.length;
    var magicNumber = Math.random() * totalRate;
    for (let i = 0; i < list.length; i++) {
        const item = list[i];
        if (magicNumber < i + 1) {
            return item;
        }
    }
}

function _chooseCardsInGroup(group)
{
    var cards = null;
    if(group.up && group.up.cards && group.up.rate > 0)
    {
        let magicNumber = Math.random();
        if(magicNumber < group.up.rate)
        {
            //choose in up
            cards = group.cards.filter(c=>group.up.cards.find(u=>u == c));
        }
        else{
            //choose except in up
            cards = group.cards.filter(c=>group.up.cards.find(u=>u != c));
        }
    }
    else{
        //choose in group
        cards = group.cards.map(c=>c);
    }
    return cards;
}


export default Pool;