function PoolGroup(param){
    this.groupId = undefined;
    this.cards = [];
    this.up = [];
    this.rate = [];
    Object.assign(this,param);
}

PoolGroup.prototype.toModel = function(){
    return {
        groupId: this.groupId,
        cards: this.cards,
        rate: this.rate
    }
}

export default PoolGroup;
