const axios = require('axios').default;
const _base = process.env.VUE_APP_DATA_BASE;

function fetchMeta(){
    return axios.get(resolveUrl('~/index.json'));
}

function fetchGame(path){
    return axios.get(resolveUrl(path));
}

function resolveUrl(path){
    if(typeof path == 'string')
    {
        return path.replace(/^~\//,_base);
    }
    return '';
}

export default {
    fetchMeta,fetchGame
}