import Group from './group';
import Card from './card';
import Pool from './pool';
import { randomId } from './randomId';

function CardBox() {
    this.cards = [];
    this.groups = [];
    this.pools = [];
    this.defaultGroup = CardBox.defaultGroup;
    const that = this;

    //cards
    this.addCard = function (inputCard) {
        if (!inputCard) {
            return;
        }

        if (!inputCard.id) {
            inputCard.id = randomId('card_', this.cards.map(c => c.id));
        }

        if (this.getCard(inputCard.id)) {
            console.error('Card ' + inputCard.id + ' is already existed, the new card will not be put inside box.');
        }
        else {
            let card = card instanceof Card ? inputCard : new Card(inputCard);
            let defaultGroup = this.defaultGroup;

            Object.defineProperty(card, '_group', {
                get() {
                    return that.groups.find(m => m.id == this.group) || defaultGroup;
                }
            })

            this.cards.push(card);
            return inputCard;
        }
    }

    this.addCards = function (inputCardList) {
        var result = []
        for (let i = 0; i < inputCardList.length; i++) {
            const card = inputCardList[i];
            var appendedItem = this.addCard(card);
            if (appendedItem)
                result.push(appendedItem);
        }
        return result;
    }

    this.removeCard = function (id) {
        this.cards.splice(this.cards.findIndex(m=>m.id == id), 1);
    }

    this.getCard = function (id) {
        return this.cards.find(c => c.id == id);
    }

    //groups
    this.getGroup = function (id) {
        return this.groups.find(g => g.id == id);
    }

    this.addGroup = function (param) {
        var group = new Group(param);
        this.groups.push(group);
        return group;
    }

    this.addGroups = function (list) {
        for (let i = 0; i < list.length; i++) {
            const group = list[i];
            this.addGroup(group);
        }
    }

    this.removeGroup = function (target) {
        this.groups.splice(this.groups.findIndex(target), 1);
    }

    //pool
    this.addPool = function (model) {
        var pool = new Pool(model);
        pool.cardBox = this;
        pool.loadCardboxGroups();
        this.pools.push(pool);
    }

    this.addPools = function (arr) {
        arr.forEach((data) => that.addPool(data))
    };

    this.removePool = function (target) {
        this.pools.splice(this.pools.findIndex(target), 1);
    }

    this.setModel = function (model) {
        this.pools.splice(0, this.pools.length);
        this.cards.splice(0, this.cards.length);
        this.groups.splice(0, this.groups.length);

        if (Array.isArray(model.groups))
            this.addGroups(model.groups);
        if (Array.isArray(model.cards))
            this.addCards(model.cards);
        if (Array.isArray(model.pools))
            this.addPools(model.pools);
    }

}

CardBox.prototype.toModel = function () {
    return {
        cards: this.cards.map(c => c.toModel()),
        groups: this.groups.map(g => g.toModel()),
        pools: this.pools.map(p => p.toModel())
    }
}

CardBox.prototype.clear = function(){
    this.cards.clear();
    this.groups.clear();
    this.pools.clear();
}

CardBox.createDefaultCard = function (param) {
    let card = new Card(param);
    Object.defineProperty(card, '_group', {
        get() {
            return CardBox.defaultGroup;
        }
    })
    return card;
}

CardBox.defaultGroup = new Group({
    id: 'Default',
    color: '#AAAAAA',
    name: '未定義',
});






export default CardBox;